import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { Link } from 'react-router-dom';
import Swal from 'sweetalert2';
import Menu from '../componentes/Menu';
import Loading from '../componentes/Cargando';
import UsuarioLista from './UsuarioLista'; // Cambiar a UsuarioLista
import UsuarioModal from './UsuarioModal'; // Cambiar a UsuarioModal

function Usuario() {
    const [datos, setDatos] = useState([]);
    const [cargando, setCargando] = useState(true);
    const [usuarioEditando, setUsuarioEditando] = useState(null);
    const [values, setValues] = useState({
        u_nombre: '',
        u_email: '',
        u_pass: '',
        u_rol: ''
    });

    // Carga lista de usuarios al entrar
    useEffect(() => {
        const cargarDatos = async () => {
            try {
                const respuesta = await axios.get('http://localhost:3001/usuario/usuario'); // Cambiar a la ruta de usuario
                setDatos(respuesta.data);
            } catch (error) {
                console.error('Error al cargar los datos:', error);
                Swal.fire({
                    icon: "error",
                    title: "Oops...",
                    text: "No se pudo cargar los datos!",
                });
            } finally {
                setCargando(false);
            }
        };

        cargarDatos();
    }, []);

    // Muestra esto si no hay respuesta del servidor
    if (cargando) {
        return <Loading />;
    }

    const handleChanges = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value });
    };

    const manejarEdicion = (info) => {
        setUsuarioEditando(info);
        const data = {
            u_nombre: info.u_nombre,
            u_email: info.u_email,
            u_pass: info.u_pass,
            u_rol: info.u_rol
        };
        setValues(data);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            await axios.put(`http://localhost:3001/usuario/usuario/${usuarioEditando.u_id}`, values); // Cambiar a la ruta de usuario
            Swal.fire('Éxito', 'Usuario actualizado correctamente', 'success');
            const respuesta = await axios.get('http://localhost:3001/usuario/usuario'); // Cambiar a la ruta de usuario
            setDatos(respuesta.data);
            setUsuarioEditando(null);
        } catch (error) {
            console.error('Error al actualizar el usuario:', error);
            Swal.fire('Error', 'No se pudo actualizar el usuario', 'error');
        }
    };

    const manejarEliminacion = async (id) => {
        const confirmacion = await Swal.fire({
            title: '¿Estás seguro?',
            text: "¡No podrás revertir esto!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Sí, eliminarlo!'
        });

        if (confirmacion.isConfirmed) {
            try {
                await axios.delete(`http://localhost:3001/usuario/usuario/${id}`); // Cambiar a la ruta de usuario
                Swal.fire('Eliminado!', 'El usuario ha sido eliminado.', 'success');
                const respuesta = await axios.get('http://localhost:3001/usuario/usuario'); // Cambiar a la ruta de usuario
                setDatos(respuesta.data);
            } catch (error) {
                console.error('Error al eliminar el usuario:', error);
                Swal.fire('Error', 'No se pudo eliminar el usuario', 'error');
            }
        }
    };

    return (
        <div>
            <Menu />
            <div className='container py-5'>
                <Link className='btn btn-primary my-2' to="/nusuario">Crear Usuario</Link> {/* Cambiar a la ruta de crear usuario */}
                <UsuarioLista
                    datos={datos}
                    manejarEdicion={manejarEdicion}
                    manejarEliminacion={manejarEliminacion}
                />
            </div>
            {usuarioEditando && (
                <UsuarioModal
                    usuarioEditando={usuarioEditando}
                    values={values}
                    handleChanges={handleChanges}
                    handleSubmit={handleSubmit}
                    closeModal={() => setUsuarioEditando(null)}
                />
            )}
        </div>
    );
}

export default Usuario;