import React, { useEffect, useState } from 'react';
import Menu from './componentes/Menu';
import { Link } from 'react-router-dom';
import { Card, Row, Col } from 'react-bootstrap';
import axios from 'axios';
import Loading from './componentes/Cargando';

function Home() {
  const [proyectos, setProyectos] = useState([]);
  const [userCounts, setUserCounts] = useState({
    Administrador: 0,
    Desarrollador: 0,
    Tester: 0
  });
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchProyectos = async () => {
      try {
        const response = await axios.get('http://localhost:3001/proyecto/dashproyecto'); // Cambia la URL según tu API
        setProyectos(response.data);
      } catch (error) {
        console.error("Error fetching proyectos:", error);
      } finally {
        setLoading(false);
      }
    };

    const fetchUserCounts = async () => {
      try {
        const roles = ['Desarrollador', 'Tester'];
        const counts = {};

        for (const rol of roles) {
          const resp = await axios.get(`http://localhost:3001/usuario/usuarioXR/${rol}`);
          counts[rol] = resp.data.count; // Asegúrate de que la respuesta tenga la propiedad 'count'
        }

        setUserCounts(counts);
      } catch (err) {
        console.error("Error fetching user counts:", err);
      }
    };

    fetchProyectos();
    fetchUserCounts();
  }, []);

  if (loading) {
    return <Loading />; // Muestra un componente de carga mientras se obtienen los datos
  }

  return (
    <div>
      <Menu />
      <div className="container mt-4">
        <h2 className="text-center">Resumen</h2>
        <Row className="mt-4">
          <Col md={4}>
            <Card>
              <Card.Body>
                <Card.Title>Total de Proyectos</Card.Title>
                <Card.Text>
                  {proyectos.length} {/* Muestra la cantidad de proyectos */}
                </Card.Text>
                <Link to="/Proyecto" className="btn btn-primary">Ver Proyectos</Link>
              </Card.Body>
            </Card>
          </Col>

          {/* Tarjeta para mostrar conteo de usuarios en un solo Card */}
          <Col md={4}>
            <Card>
              <Card.Body>
                <Card.Title>Conteo de Usuarios</Card.Title>
                <Card.Text>
                  <strong>Desarrolladores:</strong> {userCounts.Desarrollador} <br />
                  <strong>Testers:</strong> {userCounts.Tester}
                </Card.Text>
              </Card.Body>
            </Card>
          </Col>
        </Row>
      </div>
    </div>
  );
}

export default Home;