import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Menu from '../componentes/Menu';
import Swal from 'sweetalert2';
import axios from 'axios';

function Nprueba() {
  const [values, setValues] = useState({
    c_prueba: '',
    c_escenario: '',
    c_descripcion: '',
    c_resultado_esperado: '',
    c_prioridad: '' // Inicialmente vacío
  });

  const navigate = useNavigate();

  const handleChanges = (e) => {
    setValues({ ...values, [e.target.name]: e.target.value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    // Validar que se haya seleccionado una prioridad
    if (!values.c_prioridad) {
      Swal.fire({
        icon: "warning",
        title: "Advertencia",
        text: "Por favor, selecciona una prioridad.",
      });
      return; // Detener el envío del formulario
    }

    try {
      const resp = await axios.post('http://localhost:3001/prueba/prueba', values);
      if (resp.status === 200) {
        Swal.fire("Se registró con éxito!");
        navigate('/Prueba');
      } else {
        Swal.fire({
          icon: "error",
          title: "Oops...",
          text: "No se pudo realizar la acción!",
        });
      }
    } catch (err) {
      console.error(err);
      Swal.fire({
        icon: "error",
        title: "Error",
        text: "Ocurrió un error al registrar la prueba!",
      });
    }
  };

  return (
    <div>
      <Menu />
      <div className="d-flex justify-content-center align-items-center">
        <div className="p-3 bg-white rounded col-12 col-md-6 col-xl-4">
          <h3 className="text-center">Nuevo Prueba</h3>
          <form onSubmit={handleSubmit} className="form-control">
            <div className="mb-3">
              <label htmlFor="c_prueba" className="form-label">Nombre de la Prueba</label>
              <input
                type="text"
                className="form-control"
                name="c_prueba"
                id="c_prueba"
                placeholder="Ejemplo"
                value={values.c_prueba}
                onChange={handleChanges}
                required
              />
            </div>

            <div className="mb-3">
              <label htmlFor="c_escenario" className="form-label">Escenario de la Prueba</label>
              <input
                type="text"
                className="form-control"
                name="c_escenario"
                id="c_escenario"
                placeholder="-----"
                value={values.c_escenario}
                onChange={handleChanges}
                required
              />
            </div>

            <div className="mb-3">
              <label htmlFor="c_descripcion" className="form-label">Descripción de la Prueba</label>
              <input
                type="text"
                className="form-control"
                name="c_descripcion"
                id="c_descripcion"
                placeholder="-----"
                value={values.c_descripcion}
                onChange={handleChanges}
                required
              />
            </div>

            <div className="mb-3">
              <label htmlFor="c_resultado_esperado" className="form-label">Resultado esperado de la Prueba</label>
              <input
                type="text"
                className="form-control"
                name="c_resultado_esperado"
                id="c_resultado_esperado"
                placeholder="-----"
                value={values.c_resultado_esperado}
                onChange={handleChanges}
                required
              />
            </div>

            <div className="mb-3">
              <label className="form-label">Prioridad</label>
              <div>
                <div className="form-check">
                  <input
                    type="radio"
                    className="form-check-input"
                    name="c_prioridad"
                    id="c_prioridadBaja"
                    value="Baja"
                    checked={values.c_prioridad === "Baja"}
                    onChange={handleChanges}
                  />

                  <label className="form-check-label" htmlFor="c_prioridadBaja">Baja</label>
                </div>
                <div className="form-check">
                  <input
                    type="radio"
                    className="form-check-input"
                    name="c_prioridad"
                    id="c_prioridadMedia"
                    value="Media"
                    checked={values.c_prioridad === "Media"}
                    onChange={handleChanges}
                  />
                  <label className="form-check-label" htmlFor="c_prioridadMedia">Media</label>
                </div>
                <div className="form-check">
                  <input
                    type="radio"
                    className="form-check-input"
                    name="c_prioridad"
                    id="c_prioridadAlta"
                    value="Alta"
                    checked={values.c_prioridad === "Alta"}
                    onChange={handleChanges}
                  />
                  <label className="form-check-label" htmlFor="c_prioridadAlta">Alta</label>
                </div>
              </div>
            </div>

            <div className='row'>
              <div className='col-12 col-md-6 col-xl-6'>
                <button type="submit" className='btn btn-primary'>
                  <i className='bi bi-floppy-fill'></i> Crear
                </button>
              </div>
              <div className='col-12 col-md-6 col-xl-6 my-2'>
                <Link to='/Prueba' className='btn btn-danger'>
                  <i className="bi bi-x-circle"></i> Cancelar
                </Link>
              </div>
            </div>
          </form>
        </div>
      </div>
    </div>
  );
}

export default Nprueba;