import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import Swal from 'sweetalert2';
import axios from 'axios';
import logo from './pages/assets/logo.jpg'; // Asegúrate de que la ruta sea correcta

function Login() {
    const [values, setValues] = useState({
        email: '',
        pass: ''
    });

    const navigate = useNavigate();

    const handleChanges = (e) => {
        setValues({ ...values, [e.target.name]: e.target.value });
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        try {
            const resp = await axios.post('http://localhost:3001/auth/login', values);
            if (resp.status === 200) {
                Swal.fire("Te has logueado!");
                navigate('/home');
            } else {
                Swal.fire({
                    icon: "error",
                    title: "Oops...",
                    text: "Usuario o Contraseña incorrecta!",
                });
            }
        } catch (err) {
            Swal.fire({
                icon: "error",
                title: "Oops...",
                text: "Error!",
            });
            console.log(err);
        }
    };

    return (
        <div className="d-flex vh-100 justify-content-center align-items-center App">
            <div className="p-3 bg-white rounded col-12 col-md-6 col-xl-4">
                <div className="text-center mb-3">
                    <img src={logo} alt="Logo" className="img-fluid" style={{ width: '200px', height: '150px' }} />
                </div>
                <h1 className=" text-center">Loguearse</h1>
                <form onSubmit={handleSubmit} >
                    <div className="mb-3">
                        <label htmlFor="email" className="form-label">Correo</label>
                        <input
                            type="email"
                            className="form-control"
                            name="email"
                            id="email"
                            aria-describedby="Correo"
                            placeholder="example@mail.com"
                            onChange={handleChanges}
                            required
                        />
                    </div>

                    <div className="mb-3">
                        <label htmlFor="pass" className="form-label">Password</label>
                        <input
                            type="password"
                            className="form-control"
                            name="pass"
                            id="pass"
                            placeholder=""
                            onChange={handleChanges}
                            required
                        />
                    </div>

                    <button className="btn btn-primary">
                        Loguearse
                    </button>
                </form>
                <Link to='/Home'>Home</Link>
            </div>
        </div>
    );
}

export default Login;